
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import Scroll from 'assembly/scroll/scroll'
import Loading from 'assembly/loading/loading'

const PAGESIZE = 20

export default {
components: {
XHeader,
Divider,
Scroll,
Loading,
Datetime,
Actionsheet
},
data () {
return {
disArr: [],
listenScroll: true,
hasMore: true,
pageNo: 1,
time: '',
billSource: '',
visibility: false,
inited: false,
pullup: true,
typeMenu: {},
typeSelected: false,
typeName: '全部'
}
},
methods: {
loadMore () {
if (!this.hasMore) {
return
}
this.pageNo ++
this._getDiscountList()
},
change () {
this.pageNo = 1
this.disArr = []
this.hasMore = true
this._getDiscountList()
},
toDetail (id) {
this.$router.push({
path: '/discountDetail',
query: {
id: id
}
})
},
setType (key, item) {
this.billSource = key
this.typeName = item.label
this.pageNo = 1
this.disArr = []
this.hasMore = true
this._getDiscountList()
},
onCancel () {
this.time = ''
this._getDiscountList()
},
formatePrice (num) {
return num / 100
},
_getDiscountList () {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候')
return
}
this.inited = true
let data = {
pagingQuery: {
pageIndex: this.pageNo,
pageSize: PAGESIZE
},
billSource: this.billSource
}
if (this.time) {

data.startTime = this.time + '-01'
data.endTime = this.$util.getMonthLastDay(this.time)
}
this.$tupHttp(this, 'front/account/discount/v1', 'POST', data).then(res => {
this.inited = false
let disArr = res.data.models.map(item => {
item.amount = this.formatePrice(item.amount)
return item
})
if (this.pageNo === 1 && !this.disArr.length) {
this.disArr = disArr
} else {
this.disArr = this.disArr.concat(disArr)
}
if (res.data.models.length < PAGESIZE) { // 是否有下一页
this.hasMore = false
}
setTimeout(() => {
this.$refs.scroll.refresh()
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_getDicts () {
let data = ['bill_source']
this.$tupHttp(this, 'front/dict/getDicts', 'POST', data).then(res => {
this.dicts = res.data.model
this.typeMenu = this.dicts.bill_source.map((item, index) => {
let sItem = {}
sItem.label = item.dictName
sItem.value = item.dictValue
return sItem
})
this.typeMenu.unshift({label: '全部', value: ''})
})
}
},
created () {
this._getDicts()
this._getDiscountList()
}
}



